<template>
  <v-dialog
    v-model="dialog"
    persistent
    max-width="600px"
  >
    <v-card>
      <v-container fluid>
        <v-row class="d-flex align-center pt-3">
          <v-col cols="12" md="4" class="py-0">
            <!-- <v-autocomplete
              v-model="player.name"
              :items="items"
              outlined
              dense
              chips
              small-chips
              label="Full Name"
              multiple
            ></v-autocomplete> -->
            <v-text-field
              v-model="player.name"
              label="Full Name"
              outlined
              dense
              type="text"
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="4" class="py-0">
            <v-text-field
              v-model.number="player.jersey"
              label="Jersey"
              outlined
              dense
              type="number"
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="4" class="py-0 mb-6">
            <v-checkbox
              v-model="player.substitute"
              label="Substitute"
            ></v-checkbox>
          </v-col>
        </v-row>
        <v-row class="d-flex justify-center pb-5">
          <v-btn
            dark
            color="primary"
            outlined
            class="px-10"
            @click="$emit('cancel')"
          >
            Close
          </v-btn>
          <v-btn
            dark
            color="primary"
            @click="addPlayer()"
          >
            <v-icon dark>
              add
            </v-icon>
            Add Player
          </v-btn>
        </v-row>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'ModalAdd',
  props: {
    dialog: Boolean
  },
  data: () => ({
    player: {
      jersey: null,
      name: "",
      replacement: null,
      goalScoredBby: [],
      yellowCard: null,
      redCard: null,
      substitute: false
    }
  }),
  methods: {
    addPlayer () {
      this.$emit('add', this.player);
      this.player = {
        jersey: null,
        name: "",
        replacement: null,
        goalScoredBby: [],
        yellowCard: null,
        redCard: null,
        substitute: false
      }
    }
  }
}
</script>