<template>
  <v-card>
    <v-container fluid>
      <v-row class="d-flex align-center pt-3">
        <v-col cols="12" md="4" class="py-0">
          <v-autocomplete
            v-model="datasheet.team"
            :items="teams"
            item-text="fullName"
            item-value="fullName"
            outlined
            dense
            label="Team"
            @change="teamSelected()"
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="2" class="py-0">
          <v-text-field
            v-model.number="datasheet.journey"
            label="journey"
            outlined
            dense
            type="number"
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="2" class="py-0">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="datasheet.date"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="datasheet.date"
                label="Date"
                prepend-inner-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                dense
                outlined
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="datasheet.date"
              no-title
              scrollable
            >
              <v-spacer></v-spacer>
              <v-btn
                text
                color="primary"
                @click="menu = false"
              >
                Cancel
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(datasheet.date)"
              >
                OK
              </v-btn>
            </v-date-picker>
          </v-menu>
        </v-col>
        <v-col cols="12" md="3" class="text-center justify-center align-center py-0" justify="center">
          <v-radio-group
            v-model="datasheet.condition"
            row
            class="mt-0 pt-0 justify-center align-center"
            justify="center"
          >
            <v-radio
              label="Home"
              value="home"
              class="mb-0 pb-0"
            ></v-radio>
            <v-radio
              label="Guest"
              value="guest"
            ></v-radio>
          </v-radio-group>
        </v-col>
        <v-col cols="12" md="4" class="py-0">
          <v-autocomplete
            v-model="datasheet.adversary"
            :items="teams"
            item-text="fullName"
            item-value="fullName"
            outlined
            dense
            chips
            small-chips
            label="Adversary"
          ></v-autocomplete>
        </v-col>
        <v-col cols="12" md="4" class="py-0">
          <v-combobox
            v-model="datasheet.footballField"
            :items="footballfields"
            outlined
            dense
            chips
            small-chips
            label="Football field"
          ></v-combobox>
        </v-col>
      </v-row>

      <v-divider></v-divider>

      <v-row>
        <v-col cols="12" md="12">
          <v-card>
            <v-data-table
              dense
              fixed-header
              :hide-default-footer="true"
              disable-pagination
              height="280px"
              :headers="headers"
              :items="datasheet.players"
              item-key="name"
              class="elevation-1"
            >
              <template v-slot:top>
                <v-toolbar
                  flat
                >
                  <v-toolbar-title>Starting eleven</v-toolbar-title>
                  <v-spacer></v-spacer>
                  <v-btn
                    dark
                    color="primary"
                    @click="dialog = !dialog"
                  >
                    <v-icon dark>
                      add
                    </v-icon>
                    Add Player
                  </v-btn>
                </v-toolbar>
              </template>
              <template v-slot:item.replacement="{item}">
                <div class="mt-0 pt-0 text-center align-center" style="height: 25px;">
                  <v-checkbox
                    v-model="item.replacement"
                    class="pt-0 mt-0"
                  ></v-checkbox>
                </div>
              </template>
              <template v-slot:item.substitute="{item}">
                <div class="mt-0 pt-0 text-center align-center" style="height: 25px;">
                  <v-checkbox
                    v-model="item.substitute"
                    class="pt-0 mt-0"
                  ></v-checkbox>
                </div>
              </template>
              <template v-slot:item.goalScoredBby="{item}">
                <div class="d-flex flex-row text-center align-center" style="height: 40px;">
                  <div v-for="(goal, index) in item.goalScoredBby" :key="index" class="success--text" style="margin-right: 3px">
                    <v-chip
                      class="mr-1"
                      close
                      @click:close="removeGoal(index, item)"
                    >
                      {{ goal }}
                    </v-chip>
                  </div>
                  <div>
                    <v-btn
                      dark
                      icon
                      color="primary"
                      @click="addGoal(item)"
                    >
                      <v-icon dark>
                        add
                      </v-icon>
                    </v-btn>
                  </div>
                </div>
              </template>
              <template v-slot:item.yellowCard="{item}">
                <div class="text-center align-center" style="height: 40px; width: 60px">
                  <v-text-field
                    v-model.number="item.yellowCard"
                    outlined
                    dense
                    type="number"
                  ></v-text-field>
                </div>
              </template>
              <template v-slot:item.jersey="{item}">
                <div class="text-center align-center" style="height: 40px; width: 60px">
                  <v-text-field
                    v-model.number="item.jersey"
                    outlined
                    dense
                    type="number"
                  ></v-text-field>
                </div>
              </template>
              <template v-slot:item.actions="{ item }">
                <v-icon
                  small
                  color="error"
                  @click="removeItem(item)"
                >
                  mdi-delete
                </v-icon>
              </template>
            </v-data-table>
          </v-card>
        </v-col>
        <v-col cols="12">
          <v-btn
            dark
            block
            color="primary"
            @click="addRecord()"
          >
            <v-icon dark>
              {{ buttonDetails.icon }}
            </v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
    <modal-add :dialog="dialog" @cancel="dialog = false" @add="addPlayer" />
    <v-dialog
      v-model="dialogGoal"
      persistent
      max-width="300px"
    >
      <v-card>
        <div class="px-5 pt-5">
          <v-select
            v-model="goalScoredBby"
            :items="goalOptions"
            outlined
            dense
          ></v-select>
        </div>
        <div class="d-flex justify-center pb-5">
          <v-btn
            dark
            color="primary"
            outlined
            class="px-5"
            @click="dialogGoal = !dialogGoal"
          >
            Close
          </v-btn>
          <v-btn
            dark
            color="primary"
            @click="addNewGoal()"
          >
            <v-icon dark>
              add
            </v-icon>
            Add
          </v-btn>
        </div>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import ModalAdd from './../components/ModalAdd';
import listTeams from "../libary/teams";
import firebase from "@/firebaseConfig";
const db = firebase.firestore();
export default {
  name: 'RecordsAdd',
  components: {
    ModalAdd
  },
  data: () => ({
    datasheet: {
      adversary: "",
      condition: "",
      date: null,
      footballField: "",
      journey: null,
      players: [],
      team: ""
    },
    
    dialog: false,
    dialogGoal: false,
    footballfields: [
      "Tchuela Field",
      "Mucacaza Field",
      "Chiquizela Field",
      "Agri-Sul Field",
      "Ligongolo Field",
      "Mahungo Field",
      "Bairro Comrcial Field",
      "Baptine Field",
    ],
    goalOptions: [
      "Foot",
      "Chest",
      "Head",
      "Penalty",
      "Free",
      "self goal"
    ],
    goalScoredBby: null,
    headers: [
      {
        text: 'Jersey',
        align: 'start',
        sortable: false,
        value: 'jersey',
      },
      { text: 'Name', value: 'name' },
      { text: 'Replacement', align: 'center', value: 'replacement' },
      { text: 'Goal scored by', align: 'center', value: 'goalScoredBby' },
      { text: 'Yellow card', align: 'center', value: 'yellowCard' },
      { text: 'Red card', align: 'center', value: 'redCard' },
      { text: 'Substitute', align: 'center', value: 'substitute' },
      { text: 'Actions', align: 'center', value: 'actions' },
    ],
    menu: false,
    selectedPlay: {},
    teams: listTeams,
  }),
  computed: {
    buttonDetails () {
      if (this.$route.query.id) {
        return { text: "Edit", icon: "mdi-pencil" }
      } else {
        return { text: "Add", icon: "create_new_folder" }
      }
    }
  },
  created () {
    const vm = this;
    if (this.$route.query.id) {
      vm.datasheet.players = []
      db.collection("datasheets").doc(this.$route.query.id)
        .get().then((doc) => {
          vm.datasheet.team = doc.data().team
          vm.datasheet.journey = doc.data().journey
          vm.datasheet.date = doc.data().date
          vm.datasheet.condition = doc.data().condition
          vm.datasheet.adversary = doc.data().adversary
          vm.datasheet.footballField = doc.data().footballField
          doc.data().players.forEach((p) => {
            vm.datasheet.players.push(p)
          })
        });
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.datasheet.team = null
    this.datasheet.journey = null
    this.datasheet.date = null
    this.datasheet.condition = null
    this.datasheet.adversary = null
    this.datasheet.footballField = null
    this.datasheet.players = []
    next()
  },
  methods: {
    addGoal (player) {
      console.log(player);
      if (!player.goalScoredBby) {
        player.goalScoredBby = []
      }
      // player.goalScoredBby.push('penalty')
      this.selectedPlay = player;
      this.dialogGoal = true;
    },
    addNewGoal () {
      this.selectedPlay.goalScoredBby.push(this.goalScoredBby)
      this.goalScoredBby = null
    },
    addPlayer (player) {
      this.datasheet.players.push(Object.assign({}, player));
    },
    addRecord () {
      const vm = this;
      if (this.$route.query.id) {
        db.collection("datasheets").doc(this.$route.query.id).set(vm.datasheet)
      } else {
        db.collection("datasheets").add(vm.datasheet)
      }
      this.$router.push('/dashboard')
    },
    removeGoal (index, item) {
      item.goalScoredBby.splice(index, 1);
    },
    removeItem (item) {
      const index = this.datasheet.players.findIndex(p => p.name == item.name)
      this.datasheet.players.splice(index, 1)
    },
    teamSelected () {
      const vm = this;
      vm.datasheet.players = []
      db.collection("datasheets").where("team", "==", this.datasheet.team).orderBy("journey", "desc").limit(1)
        .get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          doc.data().players.forEach((p) => {
            vm.datasheet.players.push({
              jersey: null,
              name: p.name,
              replacement: null,
              goalScoredBby: [],
              yellowCard: null,
              redCard: null,
              substitute: false
            })
          })
        });
      });
    }
  }
}
// W!ldFr&&F0und@t!0n  www.tnr.com
</script>